<template>
  <v-layout>
    <v-app-bar fixed elevate-on-scroll :class="toggleNavbarPadding()">
      <v-img align-start class="logo" src="../assets/logo2.svg"></v-img>
      <v-container
        v-if="!isXs()"
        class="align-end d-flex flex-row-reverse pr-0"
      >
        <v-btn text color="secondary" @click="map()">Map</v-btn>
        <v-btn text color="secondary" @click="about()">About</v-btn>
        <v-btn text color="secondary" @click="home()">Home</v-btn>
      </v-container>
      <v-container v-else>
        <v-app-bar-nav-icon
          color="white"
          class="nav-bar-icon"
          @click="drawer = true"
        >
        </v-app-bar-nav-icon>
      </v-container>
    </v-app-bar>
    <v-navigation-drawer v-model="drawer" fixed temporary>
      <v-list nav dense>
        <v-list-item-group
          active-class="primary--text text--accent-4"
        >
          <v-list-item @click="home()">
            <v-list-item-icon>
              <v-icon>mdi-home</v-icon>
            </v-list-item-icon>
            <v-list-item-title>Home</v-list-item-title>
          </v-list-item>

          <v-list-item @click="about()">
            <v-list-item-icon>
              <v-icon>mdi-help-circle</v-icon>
            </v-list-item-icon>
            <v-list-item-title>About</v-list-item-title>
          </v-list-item>

          <v-list-item @click="map()">
            <v-list-item-icon>
              <v-icon>mdi-map-marker</v-icon>
            </v-list-item-icon>
            <v-list-item-title>Map</v-list-item-title>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
  </v-layout>
</template>

<script>
export default {
  name: "NavigationBar",
  methods: {
    async map() {
      await this.$router.push("/map").then((this.drawer = false));
    },
    async about() {
      if (this.$route.matched.some(({ name }) => name === "home")) {
        this.$vuetify.goTo(`#About`, this.options);
        this.drawer = false;
      } else {
        await this.$router
          .push("/")
          .then(() => {
            this.$vuetify.goTo(`#About`, this.options);
          })
          .then((this.drawer = false));
      }
    },
    async home() {
      this.$router.push("/");
      await this.$vuetify
        .goTo(`#Home`, this.options)
        .then((this.drawer = false));
    },
    isXs() {
      return this.$vuetify.breakpoint.xsOnly ? true : false;
    },
    toggleNavbarPadding() {
      return this.isXs() ? "primary nav-bar-mobile" : "primary nav-bar";
    }
  },
  computed: {
    options() {
      return {
        duration: 500,
        offset: 0,
        easing: "easeInOutCubic"
      };
    }
  },
  data() {
    return {
      drawer: false
    };
  }
};
</script>

<style scoped>
.nav-bar {
  padding-left: 10vw;
  padding-right: 10vw;
}

.nav-bar-mobile {
  padding-left: 0;
  padding-right: 0;
}

.logo {
  max-width: 180px;
  max-height: 180px;
  margin-top: 1vh;
}

.nav-bar-icon {
  float: right;
}
</style>
